<template>
  <div id="container">
  </div>
</template>

<script>
import GraphUtils from "../js/GraphUtils";
import {mapGetters} from "vuex";
import {url} from "../js/config";

export default {
  name: "Dep",
  data(){
    return{
      graphData:{
        nodes: [
          {
            id: 'Service A',
            label: 'Service A',
            description: 'aaaaaaaa',
          },
          {
            id: 'Service B',
            label: 'Service B',
            description: 'bbbbbbbb',
          },
        ],
        edges: [
          {
            source: 'Service A',
            target: 'Service B',
            label: ['AAAAaaaaaaa', 'BBBBBbbbbbbbbbbb'],
            startLabel: 'AAAAA',
            endLabel: 'BBBBB'
          }
        ]
      }
    }
  },

  computed:{
    ...mapGetters(['selectedMBSName'])
  },

  methods: {
    drawGraph(mbsName){
      this.$http.get(url + "/visualization/service/" + mbsName).then(res => {
        if(res.data.status){
          this.graphData = res.data.data
          GraphUtils.drawServiceCallGraph(this.graphData, 'container')
        }
      })
    }
  },
  mounted() {
    this.drawGraph(this.selectedMBSName)
  }
}
</script>

<style scoped>
#container{
  background-color: #B3C0D1;
  width: 100%;
  height: 100%;
  font: 300 14px 'Helvetica Neue', Helvetica;
}

</style>



