<template>
  <div id="resource">
    <el-collapse accordion>
      <el-collapse-item v-for="(dataType, index) in this.resourceVoList" :key="index">
        <template slot="title">
          <span style="margin-left: 10px; margin-right: 10px; font-weight: bold;">{{ dataType.fullName }}</span> <el-tag size="mini" type="info">{{ dataType.category }}</el-tag>
        </template>
        <div v-show="dataType.propertyVoList !== null">
          <el-table
            empty-text="No data"
            :data="dataType.propertyVoList"
            border
            style="width: 80%; margin-left: 5%">
            <el-table-column
              prop="propertyName"
              label="Property Name"
              width="300">
            </el-table-column>
            <el-table-column
              prop="typeFullName"
              label="Property Type">
              <template slot-scope="scope">
                {{scope.row.typeFullName}}
                <el-tag size="mini" type="info"  style="margin-left: 10px; margin-right: 10px">{{ scope.row.category }}</el-tag>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <el-descriptions direction="vertical" :column="1" border v-show="dataType.scope !== null"  style="width: 94%; margin-left: 3%;">
          <el-descriptions-item label="Scope">
              <span>
                <el-tag size="mini" type="info" v-for="(key, index) in dataType.scope" style="margin-left: 10px">{{ key }}</el-tag>
              </span>
          </el-descriptions-item>
        </el-descriptions>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {url} from "../../js/config";

export default {
  name: "Resource",
  data(){
    return{
      resourceVoList:[],
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 '
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 '
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 '
      }]
    }
  },
  created() {
    this.getResourceList(this.selectedMBSName, this.selectedServiceName)
  },
  watch: {
    selectedServiceName(newValue, oldValue){
      this.getResourceList(this.selectedMBSName, newValue)
    }
  },
  computed: {
    ...mapGetters(['selectedServiceName', 'selectedMBSName'])
  },
  methods:{
    getResourceList(mbsName, serviceName){
      this.$http.get(url + "/resources/" + mbsName + "/" + serviceName).then(res => {
        if(res.data.status){
          this.resourceVoList = res.data.data
        }
      })
    },
  }
}
</script>

<style scoped>

</style>



