import Vue from 'vue'
import Router from 'vue-router'
import Main from '../components/Main'
import Operation from "../components/elements/Operation";
import Resource from "../components/elements/Resource";
import Index from "../components";
import Model from "../components/Model";
import Dep from "../components/Dep";
import Scenario from "../components/Scenario";
import MyComponent from "../components/elements/MyComponent";
import Detection from "../components/Detection";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/main/',
      redirect: '/main/model',
      name: 'Main',
      component: Main,
      children: [
        {
          path: 'model',
          name: 'Model',
          component: Model,
          children:[
            {
              path: 'operation',
              name: 'Operation',
              component: Operation
            },
            {
              path: 'component',
              name: 'MyComponent',
              component: MyComponent
            },
            {
              path: 'resource',
              name: 'Resource',
              component: Resource
            },
          ]

        },
        {
          path: 'dep',
          name: 'Dep',
          component: Dep
        },
        {
          path: 'scenario',
          name: 'Scenario',
          component: Scenario
        },
        {
          path: 'detection',
          name: 'Detection',
          component: Detection
        }
      ]
    }
  ]
})



