<template>
  <div id="component">
    <el-collapse>
      <el-collapse-item v-for="(component, index) in this.componentVoList" :key="index">
        <template slot="title">
          <el-tag size="mini" style="margin-left: 10px; margin-right: 10px;">{{ component.type }}</el-tag>
          <span style="margin-right: 10px; font-weight: bold;">{{ component.fullName }}</span> <el-tag size="mini" type="info">{{ component.methodNum }}</el-tag>
        </template>
        <el-table
          empty-text="No data"
          :data="component.methodVoList" class="table-expand" border
          :cell-style="{ }"
          :header-cell-style="{ }">
          <el-table-column label="Method Name" width="250">
            <template slot-scope="scope">
              <el-popover
                placement="top-start"
                title="Signature:"
                width="500"
                trigger="click"
                :content="scope.row.signature">
                <el-button slot="reference" type="text" size="medium">{{ scope.row.name }}</el-button>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="ReturnType" width="250">
            <template slot-scope="scope">
              <el-tooltip
                :content="scope.row.returnType.fullTypeName"
                placement="bottom"
                effect="light"
                :disabled="scope.row.returnType.typeName === scope.row.returnType.fullTypeName">
                <el-button plain size="mini">{{scope.row.returnType.typeName}}</el-button>
              </el-tooltip>
            </template>
          </el-table-column>

          <el-table-column label="Parameters">
            <template slot-scope="scope">
              <el-tooltip
                v-for="(parameter, index) in scope.row.parameterTypeList"
                :key="index"
                :content="parameter.fullTypeName"
                placement="bottom"
                effect="light"
                :disabled="parameter.typeName === parameter.fullTypeName">
                <el-button plain size="mini">{{ parameter.typeName }}</el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="Relation" width="330">
            <template slot-scope="scope">
              <el-popover
                placement="left"
                width="500"
                trigger="click">
                <el-table :data="scope.row.invokerList">
                  <el-table-column width="200" property="fromMethodName" label="Method"></el-table-column>
                  <el-table-column width="200" property="fromComponentName" label="Component"></el-table-column>
                  <el-table-column width="100" property="invokeType" label="Type"></el-table-column>
                </el-table>
                <el-button slot="reference" size="mini" type="success" :disabled="scope.row.invokerList.length === 0">Invokers</el-button>
              </el-popover>
              <el-popover
                placement="bottom"
                width="500"
                trigger="click">
                <el-table :data="scope.row.invokeeList">
                  <el-table-column width="200" property="toMethodName" label="Method"></el-table-column>
                  <el-table-column width="200" property="toComponentName" label="Component"></el-table-column>
                  <el-table-column width="100" property="invokeType" label="Type"></el-table-column>
                </el-table>
                <el-button slot="reference" type="success" size="mini" :disabled="scope.row.invokeeList.length === 0">Invokees</el-button>
              </el-popover>
<!--              <el-button type="primary" size="mini">Invocation Graph</el-button>-->
            </template>
          </el-table-column>
        </el-table>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {url} from "../../js/config";

export default {
  name: "MyComponent",
  data(){
    return{
      componentVoList:[],
    }
  },
  computed: {
    ...mapGetters(['selectedServiceName', 'selectedMBSName'])
  },
  created() {
    this.getComponentList(this.selectedMBSName, this.selectedServiceName)
  },
  watch: {
    selectedServiceName(newValue, oldValue){
      this.getComponentList(this.selectedMBSName, newValue)
    }
  },
  methods:{
    getComponentList(mbsName, serviceName){
      this.$http.get(url + "/components/" + mbsName + "/" + serviceName).then(res => {
        if(res.data.status){
          this.componentVoList = res.data.data
        }
      })
    },
  }
}
</script>

<style scoped>
.table-expand {
  font-size: 10px;
  margin-left: 5%;
  margin-bottom: 5px;
  width: 90%;
}
</style>



