<template>
  <div id="dependency">

  </div>
</template>

<script>
export default {
  name: "Dependency",
  data(){
    return{
      graphData:{
        nodes: [
          {
            id: 'Service A',
            label: 'Service A',
            class: 'service',
          },

          {
            id: 'Service B',
            label: 'Service B',
            class: 'service',
          },
          {id: '/bbbbbbb/user/all/1', label: '/bbbbb/user/all/1', class:'interface'},
          {id: '/bbbbbbb/user/all/2', label: '/bbbbb/user/all/2', class:'interface'},
          {id: '/bbbbbbb/user/all/3', label: '/bbbbb/user/all/3', class:'interface'},
          {id: '/bbbbbbb/user/all/4', label: '/bbbbb/user/all/4', class:'interface'},
          {
            id: 'Service C',
            label: 'Service C',
            class: 'service',
          },
          {id: '/ccccccc/customer/all/1', label: '/ccccccc/customer/all/1', class:'interface'},
          {id: '/ccccccc/customer/all/2', label: '/ccccccc/customer/all/2', class:'interface'},
          {id: '/ccccccc/customer/all/3', label: '/ccccccc/customer/all/3', class:'interface'},
        ],
        edges: [
          {source: 'Service A', target: '/bbbbbbb/user/all/1', class:'call'},
          {source: 'Service A', target: '/bbbbbbb/user/all/2', class:'call'},
          {source: 'Service A', target: '/bbbbbbb/user/all/3', class:'call'},
          {source: 'Service A', target: '/bbbbbbb/user/all/4', class:'call'},

          {source: 'Service B', target: '/bbbbbbb/user/all/1', class:'mapping'},
          {source: 'Service B', target: '/bbbbbbb/user/all/2', class:'mapping'},
          {source: 'Service B', target: '/bbbbbbb/user/all/3', class:'mapping'},
          {source: 'Service B', target: '/bbbbbbb/user/all/4', class:'mapping'},

          {source: 'Service B', target: '/ccccccc/customer/all/1', class:'call'},
          {source: 'Service B', target: '/ccccccc/customer/all/2', class:'call'},
          {source: 'Service B', target: '/ccccccc/customer/all/3', class:'call'},

          {source: 'Service C', target: '/ccccccc/customer/all/1', class:'mapping'},
          {source: 'Service C', target: '/ccccccc/customer/all/2', class:'mapping'},
          {source: 'Service C', target: '/ccccccc/customer/all/3', class:'mapping'},

        ]
      }
    }
  }

}
</script>

<style scoped>

</style>



