<template>
  <el-container style="height: 100%; padding: 0;">
    <el-aside width="300px" style=" background-color: #B3C0D1">
      <div >
        <div><span style="margin-left: 5px; font-weight: bold">Scenario configuration</span></div>
        <el-upload
          drag
          class="upload-demo"
          ref="upload1"
          action=""
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-change="handleChange1"
          :file-list="fileList1"
          :auto-upload="false"
          :show-file-list="true"
          accept=".json, .txt"
          :limit="1"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">Drag or <em>click</em> to upload</div>
        </el-upload>
        <el-divider></el-divider>
        <div><span style="margin-left: 5px; font-weight: bold">Trace file</span></div>
        <el-upload
          drag
          class="upload-demo"
          ref="upload2"
          action=""
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-change="handleChange2"
          :file-list="fileList2"
          :show-file-list="true"
          :auto-upload="false"
          accept=".json, .txt"
          :limit="1">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">Drag or <em>click</em> to upload</div>
        </el-upload>
        <el-divider></el-divider>

        <div style="margin-top: 10px; display: flex; align-items: center; justify-content: center;">
          <el-button size="medium" type="success" @click="submitUpload" >Execute Detection</el-button>
        </div>

      </div>
    </el-aside>
    <el-main>
<!--      <div style="width: 100%;  height: calc(100% - 60px); margin: 0;">-->
<!--        <el-tabs tab-position="top" type="border-card" @tab-click="">-->
<!--          <el-tab-pane label="Failed Call" name="failed-call" style="height: calc(100% - 60px);">-->
<!--            <FailedCall></FailedCall>-->
<!--          </el-tab-pane>-->
<!--          <el-tab-pane label="Unexpected Call" name="unexpected-call">-->
<!--            <UnexpectedCall></UnexpectedCall>-->
<!--          </el-tab-pane>-->
<!--          <el-tab-pane label="Missing Call" name="missing-call">-->
<!--            <MissingCall></MissingCall>-->
<!--          </el-tab-pane>-->
<!--        </el-tabs>-->
<!--      </div>-->
      <div v-show="showResult">
        <div style="margin-bottom: 10px">
          <span style="margin-left: 5px; font-weight: bold; color: #373D41">Inconsistent Behavior of scenario [{{this.detectionReport.scenarioName}}]: </span>
        </div>
        <el-collapse>
          <el-collapse-item v-for="(behaviorEntry, index) in detectionReport.behaviorEntryList">
            <template slot="title">
              <el-tag size="mini" type="warning" style="margin-left: 10px; margin-right: 10px;">Trace</el-tag>
              <span style="margin-right: 10px; font-weight: bold;">{{ behaviorEntry.traceId }}</span> <el-tag size="mini" type="info">{{behaviorEntry.inconsistencyBehaviorList.length}}</el-tag>
            </template>
            <div>
              <el-table
                empty-text="No data"
                border
                class="table-expand"
                :data="behaviorEntry.inconsistencyBehaviorList"
                stripe
                style="width: 100%">
                <el-table-column
                  label="SpanId"
                  width="180">
                  <template slot-scope="scope">
                    <el-tag size="mini" style="margin-left: 10px; margin-right: 10px;">{{ scope.row.spanId }}</el-tag>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="fromService"
                  label="Client Service"
                  width="180">
                </el-table-column>
                <el-table-column
                  label="Client Operation">
                  <template slot-scope="scope">
                    <span>{{scope.row.fromPath}}</span>
                    <el-tag type="danger" size="mini" style="margin-left: 10px; margin-right: 10px;">{{ scope.row.fromAction }}</el-tag>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="toService"
                  label="Server Operation">
                </el-table-column>
                <el-table-column
                  label="Server Operation">
                  <template slot-scope="scope">
                    <span>{{scope.row.toPath}}</span>
                    <el-tag type="danger" size="mini" style="margin-left: 10px; margin-right: 10px;">{{ scope.row.toAction }}</el-tag>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="inconsistencyType"
                  label="Type">
                  <template slot-scope="scope">
                    <el-tag type="danger" size="mini" style="margin-left: 10px; margin-right: 10px;">{{ scope.row.inconsistencyType }}</el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <el-divider></el-divider>
          </el-collapse-item>
        </el-collapse>
      </div>
      <el-empty v-show="showEmpty" description="Waiting detection results..."></el-empty>
    </el-main>
  </el-container>


</template>

<script>
import {url} from "../js/config";
export default {
  name: "Detection",
  data() {
    return {
      showResult: false,
      showEmpty: true,
      fileList1: [],
      fileList2: [],
      file1: '',
      file2: '',
      formData : '',
      detectionReport:{
        scenarioName:"-",
        behaviorEntryList: []
      }
    };
  },
  methods: {
    submitUpload() {
      if(this.file1 === ''){
        this.$message.warning('No scenario configuration file is selected!')
      }else if(this.file1 === ''){
        this.$message.warning('No scenario configuration file is selected!')
      }else{
        // this.$refs.upload.submit();
        let formData = new FormData();
        formData.append("file1", this.file1.raw);
        formData.append("file2", this.file2.raw);
        const config = {
          headers: { 'Content-Type': 'multipart/form-data;charset=utf-8' }
        };
        this.$http.post(
          url + "/detection/all-config", formData, config
        ).then( res => {
          if(res.data.status){
            this.$message.success('Uploading succeed!');
            this.detectionReport = res.data.data
            this.showResult = true;
            this.showEmpty = false;
            // this.fileList1 = [];
            // this.fileList2 = [];
          }else{
            this.$message.error("Uploading failed!");
          }
        })
      }
    },
    handleChange1(file, fileList){
      if(fileList.length === 0){
        this.file1 = ''
      }else{
        this.file1 = fileList[0]
      }
      console.log(file, fileList, this.fileList1, this.fileList2)
    },
    handleChange2(file, fileList){
      if(fileList.length === 0){
        this.file2 = ''
      }else{
        this.file2 = fileList[0]
      }
      console.log(file, fileList, this.fileList1, this.fileList2)
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log('fileList1', this.fileList1)
      console.log('fileList2', this.fileList2)
      console.log(file);
    }
  }
}
</script>

<style scoped>
>>>.el-upload-dragger {
  background-color: #fff;
  margin-top: 10px;
  margin-left: 20px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 260px;
  height: 150px;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
}

.table-expand {
  font-size: 12px;
  margin-left: 3%;
  margin-bottom: 5px;
  width: 94%;
}
</style>



