<template>
  <div id="operation">
    <el-collapse>
      <el-collapse-item v-for="(operation, index) in this.operationVoList" :key="index">
        <template slot="title">
          <el-tag size="mini" type="warning" style="margin-left: 10px; margin-right: 10px;">{{ operation.voRequestMethod }}</el-tag>
          <span style="margin-right: 10px; font-weight: bold;">{{ operation.voName }}</span> <el-tag size="mini" type="info">{{ operation.voPath }}</el-tag>
        </template>
        <el-descriptions direction="vertical" size="small" :column="4" border style="width: 90%; margin-left: 5%;">
          <el-descriptions-item label="Component">
            <el-popover
              placement="top-start"
              width="400"
              trigger="click"
              :content="operation.voComponentFullName">
              <el-button slot="reference" type="text" size="medium">{{ operation.voComponentName }}</el-button>
            </el-popover>
          </el-descriptions-item>
          <el-descriptions-item label="Method">
            <el-popover
              placement="top-start"
              width="800"
              trigger="click"
              :content="operation.voMethodSignature">
              <el-button slot="reference" type="text" size="medium">{{ operation.voMethodName }}</el-button>
            </el-popover>
          </el-descriptions-item>
          <el-descriptions-item label="Response">
            <el-tooltip
              :content="operation.voResponseFullType"
              placement="bottom"
              effect="light"
              :disabled="operation.voResponseFullType === operation.voResponseType">
              <el-button plain size="mini">{{ operation.voResponseType}}</el-button>
            </el-tooltip>
          </el-descriptions-item>
          <el-descriptions-item label="Relation">
            <el-popover
              placement="left"
              width="500"
              trigger="click">
                  <el-table :data="operation.callerList">
                    <el-table-column width="200" property="service" label="Service"></el-table-column>
                    <el-table-column width="300" label="Operation">
                      <template slot-scope="scope">
                        <div>
                          <span style="font-weight: bold;">{{ scope.row.operationName }}</span>
                          <el-tag size="mini" type="warning" style="margin-left: 6px;">{{ scope.row.actionType }}</el-tag>
                        </div>
                        <div>
                          <el-tag size="mini" type="info">{{ scope.row.path }}</el-tag>
                        </div>
                      </template>
                    </el-table-column>
                  </el-table>
              <el-button slot="reference" size="mini" type="success" style="margin-left: 10px" :disabled = "operation.callerList === null || operation.callerList.length === 0">Callers</el-button>
            </el-popover>
            <el-popover
              placement="bottom"
              width="500"
              trigger="click">
              <el-table :data="operation.calleeList">
                <el-table-column width="200" property="service" label="Service"></el-table-column>
                <el-table-column width="300" label="Operation">
                  <template slot-scope="scope">
                    <div>
                      <span style="font-weight: bold;">{{ scope.row.operationName }}</span>
                      <el-tag size="mini" type="warning" style="margin-left: 6px;">{{ scope.row.actionType }}</el-tag>
                    </div>
                    <div>
                      <el-tag size="mini" type="info">{{ scope.row.path }}</el-tag>
                    </div>
                  </template>
                </el-table-column>
              </el-table>
              <el-button slot="reference" type="success" size="mini" style="margin-left: 10px" :disabled = "operation.calleeList === null || operation.calleeList.length === 0">Callees</el-button>
            </el-popover>
<!--            <el-button type="primary" size="mini" style="margin-left: 10px">Call Graph</el-button>-->
          </el-descriptions-item>
          <el-descriptions-item label="Parameter List" span="4" :v-show="operation.voParamList != null && operation.voParamList.length > 0">
            <el-table
              empty-text="No data"
              :data="operation.voParamList" class="table-expand"
              :cell-style="{ }"
              :header-cell-style="{ }">
              <el-table-column label="Position">
                <template slot-scope="scope">
                  <el-tag type="success" size="small">{{scope.row.position}}</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="Name"></el-table-column>
              <el-table-column label="Data Type">
                <template slot-scope="scope">
                  <el-tooltip
                    :content="scope.row.dataFullType"
                    placement="bottom"
                    effect="light"
                    :disabled="scope.row.dataFullType === scope.row.dataType">
                    <el-button plain size="mini">{{ scope.row.dataType }}</el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-descriptions-item>

        </el-descriptions>
      </el-collapse-item>
    </el-collapse>

<!--    <el-table-->
<!--      :data="this.operationVoList"-->
<!--      border-->
<!--      :header-cell-style="{color:'#4682B4'}"-->
<!--      :cell-style="{ background:'#DCDCDC', color:'#000000'}"-->
<!--      style="width: 100%">-->
<!--      <el-table-column type="expand">-->
<!--        <template slot-scope="props">-->
<!--          <el-table-->
<!--            :data="props.row.voParamList" class="table-expand" border-->
<!--            :cell-style="{ }"-->
<!--            :header-cell-style="{ }">-->
<!--            <el-table-column prop="name" label="Param Name"></el-table-column>-->
<!--            <el-table-column prop="dataType" label="Param Type"></el-table-column>-->
<!--            <el-table-column prop="position" label="Style"></el-table-column>-->
<!--          </el-table>-->
<!--        </template>-->
<!--      </el-table-column>-->

<!--      <el-table-column prop="voName" label="Name"></el-table-column>-->
<!--      <el-table-column prop="voPath" label="Path"></el-table-column>-->
<!--      <el-table-column prop="voRequestMethod" label="ActionType" ></el-table-column>-->
<!--      <el-table-column prop="voResponseType" label="Response Type" ></el-table-column>-->
<!--      <el-table-column prop="voComponentName" label="Component"></el-table-column>-->
<!--      <el-table-column prop="voMethodName" label="Method"></el-table-column>-->
<!--    </el-table>-->
  </div>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import utils from "../../js/utils";
import {url} from "../../js/config";

export default {
  name: "Operation",
  created() {
    this.getOperationVoList(this.selectedMBSName, this.selectedServiceName)
  },
  data(){
    return{
      operationVoList:[]
    }
  },
  computed: {
    ...mapGetters(['selectedServiceName', 'selectedMBSName'])
  },
  watch: {
    selectedServiceName(newValue, oldValue){
      this.getOperationVoList(this.selectedMBSName, newValue)
    }
  },
  methods: {
    getOperationVoList(mbsName, serviceName){
      this.$http.get(url + "/operations/" + mbsName + "/" + serviceName).then(res => {
        if(res.data.status){
          this.operationVoList = res.data.data
        }
      })
    },
  }

}
</script>

<style scoped>
.table-expand {
  font-size: 10px;
  margin-left: 2%;
  margin-bottom: 5px;
  width: 96%;
}

</style>



