<template>
  <div id="main">
    <el-container style="height: 100%; margin: 0; background-color: #FFFFFF">
      <el-header height="60px">
          <el-menu
            router
            default-active="/main/model"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b">
            <span style="float: left; margin-left: 10px; margin-top: 7px">
              <img src="../imgs/title.png" height="45" width="200" @click="returnIndex()">
            </span>
            <el-menu-item index="/main/model" style="font-size: 18px; font-family: Arial,serif; margin-left: 100px;">Model</el-menu-item>
            <el-menu-item index="/main/detection" style="font-size: 18px; font-family: Arial,serif">Detection</el-menu-item>
<!--            <el-menu-item index="/main/dep" style="font-size: 18px; font-family: Arial,serif">Dependency</el-menu-item>-->
<!--            <el-menu-item index="/main/scenario" style="font-size: 18px; font-family: Arial,serif">Scenario</el-menu-item>-->
            <span style="float: right; color: #DCDCDC; margin-right: 20px; margin-top: 10px">
                MBS:
              <span style="color: #87CEFA;">{{this.mbsName}}</span>
            </span>
          </el-menu>
      </el-header>
      <el-main >
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { url } from "../js/config";
import {mapGetters, mapState} from 'vuex';
import { mapMutations } from 'vuex';
import Index from "./Index";

export default {
  name: 'Main',
  data () {
    return {
      mbsName:'',
      mbs:{},
      service:{}
    }
  },
  watch: {

  },
  methods:{
    returnIndex(){
      this.$router.push("/")
    },
    getAllMBS() {
      this.$http.get(url + "/mbs").then(res => {
        if (res.data.status) {
          this.mbsList = res.data.data
          this.setMBSList(this.mbsList)
        }
      })
    },
    getMBSByName(mbsName){
      this.$http.get(url + "/mbs/" + mbsName).then(res =>  {
          if(res.data.status){
            this.mbs = res.data.data
          }
      })
    },
    selectService(serviceName){
      console.log(serviceName)
      //localStorage.setItem('serviceName', serviceName)
      this.getServiceByName(this.selectedMBSName, serviceName)
    },
    getServiceByName(mbsName, serviceName){
      this.$http.get(url + "/service/" + mbsName + "/" + serviceName).then(res => {
        if(res.data.status){
          console.log(res.data.data.name)
          this.service = res.data.data
          this.setSelectedService(res.data.data)
        }
      })
    },
    handleSelect(index){
      //this.$router.push(index)
      //console.log(index)
    },
    ...mapMutations(['setSelectedService',"setMBSList"])
  },
  computed:{
    ...mapGetters(['selectedServiceName', 'selectedMBSName', 'selectedService', 'mbsList'])
  },
  created() {
    this.getMBSByName(this.selectedMBSName)
    this.mbsName = this.selectedMBSName
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#main{
  height: calc(100vh);
}
.el-header {
  display: flex;
  background-color: #FFFFFF;
  border-bottom-width: 0;
  color: #fff;
  margin: 0;
  padding-left: 0;
  padding-right: 0;
  /*padding-top: 10px;*/
  /*padding-left: 8px;*/
  /*font-family: "Times New Roman",serif;*/
  font-size: 20px;
}
.el-menu {
  flex: 1;
  justify-content: center;
  align-items: center;
  border-bottom: #373D41;
}

.el-aside {
  background-color: #FFFFFF;
  height: calc(100vh - 60px);
}

#aside-header-id{
  width: 100%;
  padding: 5px;
  /*font-family: 'Times New Roman',serif;*/
  font-size: 20px;
  color: #fff;
  background-color: #333333;
}

.el-button--goon {
  font-size: 18px;
  width: 100%;
  text-align: left;

  color: black;
  background-color: #FFFFFF;
  border-width: 0px;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}
.el-button--goon:focus,
.el-button--goon:hover {
  color: #1E90FF;
  background-color: white;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}
.el-button--goon:active {
  color: #FFF;
  background-color: #333333;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}


.el-main {
  background-color: #EAEDF1;
  padding: 1px;
}
</style>



