import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const mbsName = localStorage.getItem('mbsName')
const serviceName = localStorage.getItem('serviceName')

const store = new Vuex.Store({
  state(){
    return {
      selectedMBSName: mbsName,
      selectServiceName: serviceName,
      selectedService:{},
      mbsList:{},
    }
  },

  getters: {
    mbsList(state){
      return state.mbsList
    },
    selectedService(state){
      return state.selectedService
    },
    selectedMBSName(state){
      return state.selectedMBSName
    },
    selectedServiceName(state){
      return state.selectServiceName
    }
  },
  mutations: {
    setMBSList(state, mbsList){
      state.mbsList = mbsList
    },
    setSelectedService(state, service){
      state.selectServiceName = service.name
      state.selectedService = service
      localStorage.setItem('serviceName', service.name)
    },
    setSelectedMBSName(state, mbsName){
      state.selectedMBSName = mbsName
      localStorage.setItem('mbsName', mbsName)
    }
  }
})
export default store



