<template>
  <el-container style="height: 100%; padding: 0">
    <el-aside width="300px">
      <el-menu
        :default-active="this.selectedServiceName"
        class="el-menu-vertical-demo"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="margin-left: 0; margin-top: 0;"
        @select="selectService">
        <el-menu-item v-for="(serviceName, key) in this.mbs.serviceList"  :index="serviceName" style="line-height: 40px; height: 40px;">
          <i class="el-icon-share"></i>
          <span slot="title">{{serviceName}}</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main style="padding: 8px;">
      <div style="width: 100%;  height: calc(100% - 60px); margin: 0;">
        <el-tabs tab-position="top" type="border-card" @tab-click="handleClick" v-model="activeName">
          <el-tab-pane label="Operation" name="operation" style="height: calc(100% - 60px);">
            <Operation></Operation>
          </el-tab-pane>
          <el-tab-pane label="Component" name="component">
            <MyComponent></MyComponent>
          </el-tab-pane>
          <el-tab-pane label="Resource" name="resource">
            <Resource></Resource>
          </el-tab-pane>
<!--          <el-tab-pane label="Dependency" name="dependency">-->
<!--            <Dependency></Dependency>-->
<!--          </el-tab-pane>-->
        </el-tabs>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import {mapGetters, mapMutations} from "vuex";
import {url} from "../js/config";
import Operation from "./elements/Operation";
import Resource from "./elements/Resource";
import MyComponent from "./elements/MyComponent";
import Dependency from "./elements/Dependency";


export default {
  name: "Model",
  components: {Dependency, MyComponent, Resource, Operation},
  data () {
    return {
      activeName:'operation',
      mbsName:'',
      mbs:{},
      service:{}
    }
  },
  watch: {

  },
  methods:{
    getMBSByName(mbsName){
      this.$http.get(url + "/mbs/" + mbsName).then(res =>  {
        if(res.data.status){
          this.mbs = res.data.data
        }
      })
    },
    handleClick(tab, event){
      //this.$router.push(this.activeName)
      console.log(this.activeName)
      console.log(tab, event)
    },
    selectService(serviceName){
      console.log(serviceName)
      //localStorage.setItem('serviceName', serviceName)
      this.getServiceByName(this.selectedMBSName, serviceName)
    },
    getServiceByName(mbsName, serviceName){
      this.$http.get(url + "/service/" + mbsName + "/" + serviceName).then(res => {
        if(res.data.status){
          console.log(res.data.data.name)
          this.service = res.data.data
          this.setSelectedService(res.data.data)
        }
      })
    },
    handleSelect(index){
      console.log(index)
    },
    ...mapMutations(['setSelectedService'])
  },
  computed:{
    ...mapGetters(['selectedServiceName', 'selectedMBSName', 'selectedService'])
  },
  created() {
    console.log('Model.Vue')
    this.getMBSByName(this.selectedMBSName)
    this.mbsName = this.selectedMBSName
  }
}
</script>

<style scoped>

.el-aside {
  background-color: #545c64;
  height: calc(100vh - 61px);
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}
.el-menu{
  border:0!important;
}

#aside-header-id{
  width: 100%;
  padding: 5px;
  /*font-family: 'Times New Roman',serif;*/
  font-size: 20px;
  color: #fff;
  background-color: #333333;
}
#main-header-id{
  margin: 0;
  padding: 0;
  /*font-family: 'Times New Roman',serif;*/
  font-size: 20px;
  color: #fff;
  background-color: cadetblue;
}

.el-button--goon {
  font-size: 18px;
  width: 100%;
  text-align: left;

  color: black;
  background-color: #FFFFFF;
  border-width: 0px;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}
.el-button--goon:focus,
.el-button--goon:hover {
  color: #1E90FF;
  background-color: white;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}
.el-button--goon:active {
  color: #FFF;
  background-color: #333333;
  border-color: #1E90FF;
  height: 25px;
  line-height: 25px;
  padding: 4px 4px 4px 5px;
}
</style>



