import G6 from "@antv/g6"
import {dagre} from "dagre-d3";
import { GraphLayoutPredict } from '@antv/vis-predict-engine'
export default {
  async drawServiceCallGraph(data, elementId) {
    this.registerEdge()
    let width = document.getElementById(elementId).scrollWidth;
    let height = document.getElementById(elementId).scrollHeight;

    for (let i = 0; i < data.nodes.length; i++) {
      let id = data.nodes[i].id
      let length = id.length;
      data.nodes[i].size = [length * 6.5, 20]
    }

    // const {predictLayout, confidence} = await GraphLayoutPredict.predict(data);


    //G6.Util.processParallelEdges(data.edges)

    let graph = new G6.Graph({
      container: elementId,
      width,
      height,
      //fitView: true,
      fitCenter: true,
      defaultNode: this.serviceNode,
      defaultEdge: this.edge,
      // layout:{
      //   type: predictLayout
      // },
      layout: {
        type: 'force',
        linkDistance: 100,
        nodeSpacing: 60,
        preventOverlap: true,
        center: [width / 2, height / 2],
      },
      // layout: {
      //   type: 'dagre',
      //   nodeSize: [40, 20],
      //   rankdir: 'LR',
      //   align: 'UL',
      //   nodesep: 20,
      //   ranksep: 50,
      // },
      // layout: {
      //   type: 'grid',
      //   begin: [20, 20],
      //   width: width - 20,
      //   height: height - 20,
      // },
      modes: {
        default: ['drag-canvas', 'drag-node']
      },
      nodeStateStyles: {
        hover: {
          lineWidth: 2,
          stroke: '#1890ff',
          fill: '#e6f7ff',
        },
      }
    })
    graph.data(data);
    graph.render();

    graph.on('node:mouseenter', (evt) => {
      const {item} = evt;
      graph.setItemState(item, 'hover', true);
    });

    graph.on('node:mouseleave', (evt) => {
      const {item} = evt;
      graph.setItemState(item, 'hover', false);
    });
  },

  // serviceNode: {
  //   type: 'modelRect',
  //   size: [200, 60],
  //   style: {
  //     radius: 5,
  //     stroke: '#69c0ff',
  //     fill: '#ffffff',
  //     lineWidth: 1,
  //     fillOpacity: 1,
  //   },
  //   // label configurations
  //   labelCfg: {
  //     style: {
  //       fill: '#595959',
  //       fontSize: 14,
  //     },
  //     offset: 30,
  //   },
  //   // left rect
  //   preRect: {
  //     show: true,
  //     width: 4,
  //     fill: '#40a9ff',
  //     radius: 2,
  //   },
  //   // configurations for the four linkpoints
  //   linkPoints: {
  //     top: false,
  //     right: false,
  //     bottom: false,
  //     left: false,
  //     // the size of the linkpoints' circle
  //     size: 10,
  //     lineWidth: 1,
  //     fill: '#72CC4A',
  //     stroke: '#72CC4A',
  //   },
  //   // configurations for the icon
  //   logoIcon: {
  //     // whether to show the icon
  //     show: true,
  //     x: 0,
  //     y: 0,
  //     // the image url for the icon, string type
  //     img:
  //       'https://gw.alipayobjects.com/zos/basement_prod/4f81893c-1806-4de4-aff3-9a6b266bc8a2.svg',
  //     width: 16,
  //     height: 16,
  //     // adjust the offset along x-axis for the icon
  //     offset: 0,
  //   },
  //   // configurations for state icon
  //   stateIcon: {
  //     // whether to show the icon
  //     show: false,
  //     x: 0,
  //     y: 0,
  //     // the image url for the icon, string type
  //     img:
  //       'https://gw.alipayobjects.com/zos/basement_prod/300a2523-67e0-4cbf-9d4a-67c077b40395.svg',
  //     width: 16,
  //     height: 16,
  //     // adjust hte offset along x-axis for the icon
  //     offset: -5,
  //   },
  // },

  serviceNode:{
    size: [50,20],
    type: 'rect',
    anchorPoints: [
      [0, 0.25],
      [0, 0.5],
      [0, 0.75],
      [1, 0.25],
      [1, 0.5],
      [1, 0.75],
      [0.25, 0],
      [0.5, 0],
      [0.75, 0],
      [0.25, 1],
      [0.5, 1],
      [0.75, 1]
    ],
    style: {
      lineWidth: 2,
      stroke: '#5B8FF9',
      fill: '#C6E5FF',
    }
  },

  edge: {
    type: 'line',
    size: 1,
    color: '#585858',
    style: {
      endArrow: {
        path: 'M 0,0 L 8,4 L 8,-4 Z',
        fill: '#585858',
      }
    },
    labelCfg: {
      position: 'middle',
    }
  },

  registerEdge(){

    G6.registerEdge('multipleLabelsEdge', {
      options: {
        style: {
          stroke: '#585858',
        },
      },
      labelAutoRotate: true,
      draw(cfg, group) {
        const startPoint = cfg.startPoint;
        const endPoint = cfg.endPoint;
        const stroke = (cfg.style && cfg.style.stroke) || this.options.style.stroke;

        const shape = group.addShape('path', {
          attrs: {
            stroke,
            endArrow: {
              path: 'M 0,0 L 8,4 L 8,-4 Z',
              fill: '#585858',
            },
            path: [
              ['M', startPoint.x, startPoint.y],
              ['L', endPoint.x, endPoint.y],
            ],
          },
          // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
          name: 'path-shape',
        });
        if (cfg.label && cfg.label.length) {
          // the left label
          group.addShape('text', {
            attrs: {
              text: cfg.label[0],
              fill: '#595959',
              textAlign: 'start',
              textBaseline: 'middle',
              x: startPoint.x,
              y: startPoint.y - 10,
            },
            // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
            name: 'left-text-shape',
          });
          if (cfg.label.length > 1) {
            // the right label
            group.addShape('text', {
              attrs: {
                text: cfg.label[1],
                fill: '#595959',
                textAlign: 'end',
                textBaseline: 'middle',
                x: endPoint.x,
                y: endPoint.y - 10,
              },
              // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
              name: 'right-text-shape',
            });
          }
        }
        // return the keyShape
        return shape;
      },
    })
  },

  drawInterfaceCallGraph(data, elementId){

    let width = document.getElementById(elementId).scrollWidth;
    let height = document.getElementById(elementId).scrollHeight;

    data.nodes.forEach(node =>  {
      let id = node.id
      let length = id.length
      node.size = [length * 6.5, 20]
      switch(node.class){
        case 'service':{node.type = 'rect'; break;}
        case 'interface':{node.type = 'ellipse'; break;}
      }
    })

    // data.edges.forEach(edge => {
    //   switch(edge.class){
    //     case 'call': {edge.type = }
    //   }
    // })



    //G6.Util.processParallelEdges(data.edges)

    let graph = new G6.Graph({
      container: elementId,
      width,
      height,
      //fitView: true,
      fitCenter: true,
      defaultNode: {
        size: [50,20],
        labelCfg: {
          style: {
            fill: '#fff',
          },
        },
      },
      defaultEdge: {
        type: 'line',
        size: 1,
        color: '#585858',
        style: {
          endArrow: {
            path: 'M 0,0 L 8,4 L 8,-4 Z',
            fill: '#585858',
          }
        },
        labelCfg: {
          position: 'middle',
        }
      },
      layout: {
        type: 'force',
        linkDistance: 100,
        nodeSpacing: 60,
        preventOverlap: true,
        center: [width / 2, height / 2],
      },
      // layout: {
      //   type: 'dagre',
      //   nodeSize: [40, 20],
      //   rankdir: 'LR',
      //   align: 'UL',
      //   nodesep: 20,
      //   ranksep: 50,
      // },
      // layout: {
      //   type: 'grid',
      //   begin: [20, 20],
      //   width: width - 20,
      //   height: height - 20,
      // },
      modes: {
        default: ['drag-canvas', 'drag-node']
      },
      nodeStateStyles: {
        hover: {
          lineWidth: 2,
          stroke: '#1890ff',
          fill: '#e6f7ff',
        },
      }
    })
    graph.data(data);
    graph.render();

    graph.on('node:mouseenter', (evt) => {
      const {item} = evt;
      graph.setItemState(item, 'hover', true);
    });

    graph.on('node:mouseleave', (evt) => {
      const {item} = evt;
      graph.setItemState(item, 'hover', false);
    });
  }





}



