<template>
  <div class="indexPage">
    <div class="header">
      <img src="../imgs/title.png" alt="title" />
    </div>
    <div class="model-list">
      <el-table
        empty-text="No MSDM has been recorded"
        :data="mbsList"
        style="width: 100%"
        :header-cell-style="{ background:'#778899',color:'#000000'}"
        :cell-style="{ background:'#F0FFFF'}">
        <el-table-column label="MBS" header-align="center" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="routeToMain(scope.row)">{{ scope.row.name }}</el-button>
          </template>
        </el-table-column>

        <el-table-column prop="serviceCount" label="SERVICE NUM" header-align="center" align="center"></el-table-column>
        <el-table-column
          label="UPDATE DATE" header-align="center" align="center">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.updateTime }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import {url} from "../js/config";
import {mapMutations} from "vuex";

export default {
  name: "Index",
  data() {
    return {
      mbsList: []
    };
  },
  methods: {
    routeToMain(mbs) {
      //MBSvuex
      this.setSelectedMBSName(mbs.name)
      this.$router.push({name: "Main", params: {mbsName: mbs.name}})
    },
    getAllMBS() {
      this.$http.get(url + "/mbs").then(res => {
        if (res.data.status) {
          this.mbsList = res.data.data
          this.setMBSList(this.mbsList)
        }
      })
    },
    ...mapMutations(["setSelectedMBSName","setMBSList"])
  },
  created() {
    this.getAllMBS()
  }
}
</script>

<style lang="scss" scoped>
.indexPage {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  margin-top: 50px;
}

.model-list {
  margin-top: 50px;
  width: 60%;
  background-color: #373D41;
}

</style>



