<template>
  <el-container style="height: 100%; padding: 0">
    <el-aside width="300px">
      <div style="margin-top: 10px;">
        <el-input style="margin-left: 15px; width: 200px" v-model="newScenario" placeholder="Scenario Name" maxlength="20"></el-input>
        <el-button type="primary" plain @click="addNewScenario" :disabled="this.newScenario === ''">ADD</el-button>
      </div>
      <el-menu
        default-active="SC-1"
        class="el-menu-vertical-demo"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="margin-left: 0; margin-top: 0;"
        @select="selectScenario">
        <el-menu-item
          v-for="(scenario, key) in this.scenarioList"
          :index="scenario"
          style="line-height: 50px; height: 50px;"
          @contextmenu.prevent.native="openContextMenu">
          <i class="el-icon-menu"></i>
          <span slot="title">{{scenario}}</span>
        </el-menu-item>
      </el-menu>
      <div v-show="contextMenuVisible" >
        <ul :style="{left:this.left+'px',top:this.top+'px'}" class="contextmenu" >
          <li><el-button type="text" @click="deleteScenario" size="mini">Delete</el-button></li>
          <li><el-button type="text" @click="closeMenu" size="mini">Close</el-button></li>
        </ul>
      </div>
    </el-aside>
    <el-main @contextmenu.native="closeMenu">
      <div style="height: 100vh;" >
        <el-table
          empty-text="No data"
          :data="this.requestList"
          :key="timeStamp"
          border
          :header-cell-style="{color:'#4682B4'}"
          :cell-style="{ background:'#DCDCDC', color:'#000000'}"
          style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <div style="margin-top: 15px; margin-left: 10px; margin-right: 10px;">
                <el-row>
                  <el-col :span="14" class="query-form" >
                    <el-form ref="form" :model="props.row" label-width="150px" style=";padding: 8px; box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)">
                      <el-form-item label="URL">
                        <el-col :span="21">
                          <el-input placeholder="URL" v-model="props.row.url" class="input-with-select">
                            <el-select v-model="props.row.requestMethod" slot="prepend" placeholder="" style="width: 110px;">
                              <el-option label="GET" value="GET"></el-option>
                              <el-option label="POSE" value="POST"></el-option>
                              <el-option label="PUT" value="PUT"></el-option>
                              <el-option label="DELETE" value="DELETE"></el-option>
                            </el-select>
                          </el-input>
                        </el-col>
                        <el-col :span="3" >
                          <el-button type="warning" style="margin-left: 5px;">Send</el-button>
                        </el-col>
                      </el-form-item>
                      <el-form-item label="Query Parameters">
                        <div v-for="(qp, index) in props.row.queryList">
                          <el-row>
                            <el-col :span="6">
                              <el-input v-model="qp.key" placeholder="Key"></el-input>
                            </el-col>
                            <el-col class="line" :span="1">
                              <el-divider style="margin-left: 20px; margin-right: 20px;"></el-divider>
                            </el-col>
                            <el-col :span="6">
                              <el-input v-model="qp.value" placeholder="Value"></el-input>
                            </el-col>
                            <el-col :span="1">
                              <el-button type="danger" size="small" plain style="margin-left: 15px;">Del</el-button>
                            </el-col>
                          </el-row>
                        </div>
                        <el-row>
                          <el-col :span="6">
                            <el-input v-model="addedQueryParameter.key" placeholder="Key"></el-input>
                          </el-col>
                          <el-col class="line" :span="1">
                            <el-divider style="margin-left: 20px; margin-right: 20px;"></el-divider>
                          </el-col>
                          <el-col :span="6">
                            <el-input v-model="addedQueryParameter.value" placeholder="Value"></el-input>
                          </el-col>
                          <el-col :span="1">
                            <el-button type="primary" size="small" plain style="margin-left: 15px;">Add</el-button>
                          </el-col>
                        </el-row>
                      </el-form-item>
                      <el-form-item label="Header Parameters">
                        <div v-for="(hp, index) in props.row.headerList">
                          <el-row>
                            <el-col :span="6">
                              <el-input v-model="hp.key" placeholder="Key"></el-input>
                            </el-col>
                            <el-col class="line" :span="1">
                              <el-divider style="margin-left: 20px; margin-right: 20px;"></el-divider>
                            </el-col>
                            <el-col :span="6">
                              <el-input v-model="hp.value" placeholder="Value"></el-input>
                            </el-col>
                            <el-col :span="1">
                              <el-button type="danger" size="small" plain style="margin-left: 15px;">Del</el-button>
                            </el-col>
                          </el-row>
                        </div>
                        <el-row>
                          <el-col :span="6">
                            <el-input v-model="addedHeaderParameter.key" placeholder="Key"></el-input>
                          </el-col>
                          <el-col class="line" :span="1">
                            <el-divider style="margin-left: 20px; margin-right: 20px;"></el-divider>
                          </el-col>
                          <el-col :span="6">
                            <el-input v-model="addedHeaderParameter.value" placeholder="Value"></el-input>
                          </el-col>
                          <el-col :span="1">
                            <el-button type="primary" size="small" plain style="margin-left: 15px;">Add</el-button>
                          </el-col>
                        </el-row>
                      </el-form-item>
                      <el-form-item label="Body Parameters">
                        <el-row>
                          <el-col :span="14">
                            <el-input type="textarea" :autosize = "{ minRows: 2, maxRows: 10}" v-model="props.row.body"></el-input>
                          </el-col>
                        </el-row>
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary">Update</el-button>
                        <el-button>Reset</el-button>
                      </el-form-item>
                    </el-form>
                  </el-col>
                  <el-col :span="10" class="resp-form">
                    <div style="padding: 8px" class="resp-container">
                      <el-row>
                        <div style="padding: 8px">
                          <span>Response:</span>
                        </div>
                      </el-row>
                      <el-row>
                        <el-input type="textarea" :autosize = "{ minRows: 2, maxRows: 20}" v-model="props.row.body"></el-input>
                      </el-row>
                    </div>
                  </el-col>
                </el-row>
              </div>
              <el-divider></el-divider>
            </template>
          </el-table-column>
          <el-table-column
            type="index"
            width="50">
          </el-table-column>
          <el-table-column  label="Request">
            <template slot-scope="scope">
              <el-tag size="mini" type="warning" style="margin-left: 10px; margin-right: 10px;">{{ scope.row.requestMethod }}</el-tag>
              <span style="margin-right: 10px; font-weight: bold;">{{ scope.row.url }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="description" label="Description"></el-table-column>
          <el-table-column label="Operation">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" @click="moveUp(scope.$index)">MoveUp</el-button>
              <el-button type="primary" size="mini" @click="moveDown(scope.$index)">MoveDown</el-button>
              <el-button type="danger" size="mini" @click="deleteRequest(scope.$index)">Delete</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "Scenario",
  data(){
    return{
      timeStamp: 0,
      addedQueryParameter:{
        key:'',
        value:'',
      },
      addedHeaderParameter:{
        key:'',
        value:'',
      },
      select:'',
      selectedSC:"",
      contextMenuVisible: false,
      newScenario:"",
      scenarioList:["SC-1", "SC-2", "SC-3", "SC-4", "SC-5", "SC-6", "SC-7", "SC-8", "SC-9"],
      requestList:[
        {
          url: "http://localhost:8080/aaa/bbb1",
          requestMethod: "POST",
          description:"Get all users",
          headerList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token2",
              value: "uidshafnduhsj"
            }
          ],
          queryList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token1",
              value: "uidshafnduhsj"
            }
          ],
          body:"{" + "user:{\n" +
            "              username: \"zhangsan\",\n" +
            "              password: \"123456\"\n" +
            "            },\n" +
            "            order:{\n" +
            "              orderId:\"76543227\",\n" +
            "              price: 32423.00999\n" +
            "            }" +
            "}"
        },
        {
          url: "http://localhost:8080/aaa/bbb2",
          requestMethod: "POST",
          description:"Get all users",
          headerList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token2",
              value: "uidshafnduhsj"
            }
          ],
          queryList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token1",
              value: "uidshafnduhsj"
            }
          ],
          body:"{" + "user:{\n" +
            "              username: \"zhangsan\",\n" +
            "              password: \"123456\"\n" +
            "            },\n" +
            "            order:{\n" +
            "              orderId:\"76543227\",\n" +
            "              price: 32423.00999\n" +
            "            }" +
            "}"
        },
        {
          url: "http://localhost:8080/aaa/bbb3",
          requestMethod: "POST",
          description:"Get all users",
          headerList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token2",
              value: "uidshafnduhsj"
            }
          ],
          queryList:[
            {
              key: "token1",
              value: "uidshafnduhsj"
            },
            {
              key: "token1",
              value: "uidshafnduhsj"
            }
          ],
          body:"{" + "user:{\n" +
            "              username: \"zhangsan\",\n" +
            "              password: \"123456\"\n" +
            "            },\n" +
            "            order:{\n" +
            "              orderId:\"76543227\",\n" +
            "              price: 32423.00999\n" +
            "            }" +
            "}"
        }
      ],
      left: 0,
      top: 0
    }
  },
  mounted() {
    window.addEventListener("click", this.closeMenu)
    // window.addEventListener("contextmenu", this.closeMenu)
  },
  beforeDestroy() {
    window.removeEventListener("click", this.closeMenu)
    // window.removeEventListener("contextmenu", this.closeMenu)
  },
  methods:{
    deleteRequest(index){
      let num = index + 1
      this.$confirm('Confirm deleting request [' + num + ']?', '', {
        confirmButtonText: 'YES',
        cancelButtonText: 'NO',
        type: 'warning'
      }).then(() => {
        this.requestList.splice(index, 1)
        this.timeStamp++
        this.$message({
          type: 'success',
          message: 'Delete successfully!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Deletion operation has been canceled!'
        });
      });
    },
    moveUp(index){
      if(index > 0){
        console.log(index)
        let temp = this.requestList[index]
        this.requestList[index] = this.requestList[index - 1]
        this.requestList[index - 1] = temp
        this.timeStamp++
        // [this.requestList[index - 1], this.requestList[index]] = [this.requestList[index], this.requestList[index - 1]]
      }
    },
    moveDown(index){
      if(index < this.requestList.length - 1){
        console.log(index)
        let temp = this.requestList[index]
        this.requestList[index] = this.requestList[index + 1]
        this.requestList[index + 1] = temp
        this.timeStamp++
        // [this.requestList[index - 1], this.requestList[index]] = [this.requestList[index], this.requestList[index - 1]]
      }
    },
    addNewScenario(){
      if(this.newScenario !== "") {
        this.scenarioList.push(this.newScenario)
      }
    },
    selectScenario(index){
      this.selectedSC = index
      console.log(index)
    },
    deleteScenario(){
      this.$confirm('Confirm deleting [' + this.selectedSC + ']?', '', {
        confirmButtonText: 'YES',
        cancelButtonText: 'NO',
        type: 'warning'
      }).then(() => {
        this.scenarioList = this.scenarioList.filter(item => {
          return item !== this.selectedSC
        })
        this.$message({
          type: 'success',
          message: 'Delete [' + this.selectedSC + '] successfully!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Deletion operation has been canceled!'
        });
      });
      this.contextMenuVisible = false
    },
    closeMenu(){
      this.contextMenuVisible = false
    },
    openContextMenu(e) {
      e.preventDefault(); //
      e.target.click();
      this.left = e.clientX;
      this.top = e.clientY + 10;
      this.contextMenuVisible = true;
    },
  }
}
</script>

<style scoped>
/*.el-divider--vertical{*/
/*  color: #373D41;*/
/*  display:inline-block;*/
/*  width:10px;*/
/*  height:100%;		//*/
/*  margin:0 8px;*/
/*  vertical-align:middle;*/
/*  position:relative;*/
/*}*/
.el-select .el-input {
  width: 130px;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.el-aside {
  background-color: #545c64;
  height: calc(100vh - 61px);
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}
.el-menu{
  border:0!important;
}


.contextmenu {
  width: 80px;
  margin: 0;
  border: 1px solid #ccc;
  background: #fff;
  z-index: 3000;
  position: absolute;
  list-style-type: none;
  padding: 5px 0;
  border-radius: 4px;
  font-size: 14px;
  color: #333;
  box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.2);
}
.contextmenu li {
  margin: 0;
  padding: 0px 22px;

}
.contextmenu li:hover {
  background: #f2f2f2;
  cursor: pointer;
}

.contextmenu li button{
  color: #2c3e50;
}

.query-form.el-col {
  border-right: solid 10px;
}

</style>



